<template>
</template>

<script>
	export default {
		data() {
			return {
				params: {},
				page: 0
			}
		},
		onLoad(option) {
			this.params = option;
		},
		methods: {
			searchResult() {
				this.$u.api.queryKeyword({"page": this.page, "k": this.params.keyword}).then(res => {
					this.normals = this.normals.concat(res.datas)
					if (res.pageCount == res.curPage) {
						this.status = 'nomore'
					} else {
						this.status = 'loadmore'
					}
				})
			}
		}
	}
</script>

